<template>
    <div class="goods">
        <div class="goods_header">
            <i class="mobile-icon-fanhui back" @click="back()"></i>
            <h2>商品详情</h2>  
        </div> 
        <div class="content">
            <mt-swipe :auto="4000" :show-indicators="true" class="swipe">
                <mt-swipe-item>
                    <img  v-lazy="goods.imgOne" :src="goods.imgOne">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img  v-lazy="goods.imgTwo">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img  v-lazy="goods.imgThree">
                </mt-swipe-item>
            </mt-swipe>
            <div class="content_name">
                <h2>{{goods.name}}</h2>
                <p>{{goods.price | money('￥')}}</p>
            </div>
            <div class="content_buy">
                <span class="buy_num">购买数量：</span>
                <a class="reduce btn" @click="reduce(goods)">-</a>
                <input type="text" v-model="num" class="num">
                <a class="add btn" @click="add(goods)">+</a>
            </div>
            <div class="content_bar">
                <mt-navbar v-model="selected" class="tabTitle">
                    <mt-tab-item id="1">图文信息</mt-tab-item>
                    <mt-tab-item id="2">商品配置</mt-tab-item>
                </mt-navbar>

                <!-- tab-container -->
                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="1">
                        <p v-for="(v,i) in goods.Images" :key="i">
                            <img v-lazy="v.one">
                        </p>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="2">
                       <table>
                           <tr v-for="(v,i) in goods.Peizhis" :key="i">
                               <td class="PeizhiL">{{v.left}}</td>
                               <td class="PeizhiR">{{v.right}}</td>
                            </tr>
                       </table>
                    </mt-tab-container-item>
                </mt-tab-container>
            </div>
        </div>
        <div class="goods_footer">
            <span class="addCart" @click="addCart(goods)">加入购物车</span>
            <span class="buyNow" @click="buyNow">立即购买</span>     
        </div>      
    </div>
</template>

<script>
import {mapState,mapActions,mapMutations,mapGetters} from 'vuex';
import { Toast } from 'mint-ui';
export default {
  name:"Vgoods_list",
  data(){
      return {
          goods:{},
          num:1,
          selected:'1',
      }
  },
  methods:{
      back(){
        window.history.go(-1);
      },
      reduce(opt){
          if(this.num>1){
            this.num--;
            opt.value--
          }
      },
      add(opt){
          this.num++;
          opt.value++;
      },
      buyNow(){
          var self=this;
          Toast({
            message: '成功支付'+self.num*self.goods.price+'元',
            iconClass: 'icon icon-success'
         });
      },
      addCart(opt){
        //   var opt={img:this.goods.imgOne,name:this.goods.name,price:this.goods.price,num:this.num};
        //   var carts=window.sessionStorage.getItem('carts')?JSON.parse(window.sessionStorage.getItem('carts')):[];
        //   carts.push(opt);
        //   window.sessionStorage.setItem('carts',JSON.stringify(carts));
        this.$store.dispatch('addCart',opt);
      }
  },
  created(){
      var id=this.$route.query.id;
      var self=this;
      this.$http.get("../../static/goodDetail.json").then(res=>{
          res.data.goodDetails.forEach(ele => {
              if(ele.id==id){
                self.goods=ele;
              }
          });
      });
      
  }
}
</script>

<style scoped>
.goods {
  text-align: center;
}
.swipe {
  height: 9rem;
  padding-top: 0.5rem;
}
.swipe img {
  width: 80%;
}
.content_name {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: 0.3rem 0;
}
.content_name h2 {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 0.2rem;
}
.content_name p {
  color: red;
}
.content_buy {
  border-bottom: 1px solid #ccc;
  padding: 0.5rem 0;
  font-size: 0;
}
.buy_num {
  font-size: 14px;
  vertical-align: middle;
}
.content_buy .num {
  width: 0.8rem;
  height: 0.8rem;
  text-align: center;
  vertical-align: middle;
  border: none;
  border: 1px solid #ccc;
}
.content_buy .btn {
  border: 1px solid #ccc;
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  vertical-align: middle;
  font-size: 14px;
}
.content_buy .reduce {
  border-right: none;
}
.content_buy .add {
  border-left: none;
}

.goods_footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 56px;
  color: #fff;
  font-size: 16px;
}
.addCart,
.buyNow {
  position: absolute;
  bottom: 0;
  width: 50%;
}
.addCart {
  left: 0;
  background-color: orange;
}
.buyNow {
  right: 0;
  background-color: red;
}
.tabTitle {
  margin-bottom: 0.2rem;
}
table {
  width: 100%;
}
table td {
  padding: 0.3rem 0;
}
table .PeizhiL {
  width: 30%;
}
table .PeizhiR {
  width: 70%;
}
</style>



