<template>
    <div class="infos">
        <div class="infos_header">
            <i class="mobile-icon-fanhui back" @click="back()"></i>
            <h2>新闻详情</h2>  
        </div>
        <div class="content">
            <div  v-for="(news,index) in list" :key="index">
                <h3>{{news.title}}</h3>
                <span>{{news.time}}</span>
                <div>
                    <div v-for="(box,i) in news.boxs" :key="i">
                        <p >{{box.p}}</p>
                        <img v-lazy="box.img">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"Vinfo",
    data(){
        return {
            list:[]
        }
    },
    created(){
        var id=this.$route.query.id;
        this.$http.get("../../static/information.json").then(res=>{
            res.data.list.forEach(ele => {
                console.log(ele);
                if(ele.id==id){
                    this.list.push(ele)
                }
            });
        })
    },
    methods:{
        back(){
            window.history.back()
        }
    }
}
</script>

<style scoped>
.infos{
    text-align: center;
}

</style>



