<template>
    <div class="choose">
        <div class="choose_header">
            <i class="mobile-icon-fanhui back" @click="back()"></i>
            <h2>选购手机</h2>  
        </div>
        <div class="content">
            <mt-navbar v-model="selected">
                <mt-tab-item id="1">全部</mt-tab-item>
                <mt-tab-item id="2">Xplay系列</mt-tab-item>
                <mt-tab-item id="3">X系列</mt-tab-item>
                <mt-tab-item id="4">Y系列</mt-tab-item>
            </mt-navbar>

            <!-- tab-container -->
            <mt-tab-container v-model="selected" swipeable class="tabContent">
                <mt-tab-container-item id="1">
                    <ul class="all">
                        <li v-for="(v,i) in phones.chooses" :key="i" @click="$router.push({path:'/goods',query:{id:v.id}})">
                            <img v-lazy="v.ImageOne">
                            <p><b>{{v.name}}</b></p>
                            <p>{{v.nametwo}}</p>
                            <p class="price">{{v.Price | money('￥')}}</p>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                    <ul class="Xplay">
                        <li v-for="(v,i) in phones.motos" :key="i" @click="$router.push({path:'/goods',query:{id:v.id}})">
                            <img v-lazy="v.ImageOne">
                            <p><b>{{v.name}}</b></p>
                            <p>{{v.nametwo}}</p>
                            <p class="price">{{v.Price | money('￥')}}</p>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="3">
                    <ul class="X">
                        <li v-for="(v,i) in phones.MotoZs" :key="i" @click="$router.push({path:'/goods',query:{id:v.id}})">
                            <img v-lazy="v.ImageOne">
                            <p><b>{{v.name}}</b></p>
                            <p>{{v.nametwo}}</p>
                            <p class="price">{{v.Price | money('￥')}}</p>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="4">
                    <ul class="Y">
                        <li v-for="(v,i) in phones.z2s" :key="i" @click="$router.push({path:'/goods',query:{id:v.id}})">
                            <img v-lazy="v.ImageOne">
                            <p><b>{{v.name}}</b></p>
                            <p>{{v.nametwo}}</p>
                            <p class="price">{{v.Price | money('￥')}}</p>
                        </li>
                    </ul>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>

<script>
export default {
   name:'Vchhose',
   data(){
       return {
          selected:"1",
          phones:[] 
       }
   },
   methods:{
       back(){
           window.history.go(-1)
       }
   },
   created(){
       this.$http.get("../../static/choose.json").then(res=>{
           this.phones=res.data;
       })
   }
}
</script>

<style scoped>
.tabContent{
    width: 100%;
    text-align: center;
}
.price{
    color: red
}
li{
    width: 50%;
    float: left;
    margin-top: 0.5rem; 
}
img{
    width: 80%;
}
li p{
    margin-top: 0.15rem;
}
</style>



