<template>
  <div class="info">
       <Vheader></Vheader>
       <div class="content">
           <div v-for="(v,i) in informations" :key="i" :class="'cl info info'+v.id">
              <h2>{{v.title}}</h2>
              <p class="time">{{v.time}}</p>
              <img v-lazy="v.img" alt="图片" @click="open(v.id)">
              <span class="fr more" @click="open(v.id)">阅读全文</span>  
           </div>
       </div>
       <Vfooter :isFixed="isFixed"></Vfooter> 
  </div>
</template>

<script>
import Vheader from "./Vheader";
import Vfooter from "./Vfooter";
export default {
    name:"Info",
    data(){
        return{
            informations:[],
            isFixed:true
        }
    },
    components:{
        Vheader,
        Vfooter
    },
    created(){
        this.$http.get("../../static/information.json").then(res=>{
            this.informations=res.data.informations;
        })
    },
    mounted:function(){
       
    },
    methods:{
        open(id){
            this.$router.push({path:'/infos',query:{id:id}})
        }
    }

  
}
</script>


<style scoped>
.info{
    text-align: center;
    border-bottom:1px solid #ccc;
    padding: 0.15rem 0;
}
.info:last-child{
    border-bottom:none;
}
.info h2{
    font-weight: bold;
    font-size: 16px;
}
.info .time{
    margin: 0.1rem 0;
}
.info img{
    height: 176.25px;
    width: 100%;
}
.info .more{
     margin: 0.2rem 0.2rem 0;
}
</style>



